.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/*
 * Mixin sakai-icons
 * This mixin should never be called directly as its only use is to output css for icons.
 * In $defaultIcons icon classnames are mapped to existing classnames:
 * For example sakai-acadtermmanage : bi-calendar-plus, will assign the bi-calendar-plus icon
 * to the si-sakai-acadtermmanage class
 * The resulting icon classes should be used together with the si base class, like:
 * si si-sakai-acadtermmanage
 * For skin customization, this icon map can be extended by assigning a similar icon map to
 * the $customIcons varuable
 */
.navIntraTool {
  margin: 0;
  border-bottom: 1px solid var(--tool-border-color);
  padding: 0 1em 0 0;
  font-size: 0;
}
.navIntraTool:after {
  content: " ";
  display: block;
  width: 100%;
}
.navIntraTool li {
  display: inline-block;
  font-size: 14px;
  margin: 2px 0 0 4px;
}
.navIntraTool li span {
  display: inline;
}
.navIntraTool li span a {
  background: var(--tool-tab-background-color);
  border-top: 1px solid var(--tool-tab-border-color);
  border-left: 1px solid var(--tool-tab-border-color);
  border-right: 1px solid var(--tool-tab-border-color);
  color: var(--tool-tab-text-color);
  font-size: 12px;
  font-weight: normal;
  display: block;
  margin: 0;
  padding: 0 0.6em;
  line-height: 32px;
  height: 32px;
  text-decoration: none;
  text-transform: none;
  letter-spacing: normal;
  border-radius: 4px 4px 0 0;
}
.navIntraTool li span a:hover, .navIntraTool li span a:focus {
  color: var(--tool-tab-hover-text-color);
  background: var(--tool-tab-hover-background-color);
  text-decoration: none;
  position: relative;
}
.navIntraTool li span a:hover:before, .navIntraTool li span a:focus:before {
  border-top: 4px solid var(--tool-tab-hover-highlight-color);
  border-radius: 4px 4px 0 0;
  content: "";
  display: block;
  position: absolute;
  left: -1px;
  right: -1px;
  top: -1px;
}
.navIntraTool li span a:focus {
  position: relative;
  outline-offset: -3px;
}
.navIntraTool li span.current {
  color: var(--tool-tab-active-text-color);
  font-size: 12px;
  background-color: var(--tool-tab-active-background-color);
  border-top: 1px solid var(--tool-tab-border-color);
  border-left: 1px solid var(--tool-tab-border-color);
  border-right: 1px solid var(--tool-tab-border-color);
  border-radius: 4px 4px 0 0;
  position: relative;
  margin: 0;
  padding: 0 0.6em;
  line-height: 32px;
  display: block;
  margin-bottom: -1px;
}
.navIntraTool li span.current:before {
  border-top: 4px solid var(--tool-tab-active-highlight-color);
  border-radius: 4px 4px 0 0;
  content: "";
  display: block;
  position: absolute;
  left: -1px;
  right: -1px;
  top: -1px;
}
.navIntraTool li span.current:after {
  border-bottom: 1px solid var(--tool-tab-active-background-color);
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
}
.navIntraTool li span.current a {
  border: 0px none;
  font-size: 1em;
  margin: 0;
  padding: 0;
  top: 1px;
}
.navIntraTool li span.inactive {
  border-left: 0px none;
}
.instruction {
  font-size: 12px;
  color: var(--instruction-color);
}

